.popup {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, .5);
  visibility: hidden;
  opacity: 0;
  transition: visibility 0s linear 0.5s, opacity 0.5s linear;
  justify-content: center;
  align-items: center;
  display: flex;
  font-family: 'Inter', Arial, sans-serif;
}

@media screen and (max-width: 500px) {
  .popup {
    padding-left: 19px;
    padding-right: 19px;
  }
}

.popup_opened {
  visibility: visible;
  opacity: 1;
  display: flex;
  transition-delay: 0s;
}

